.btn {
  display: inline-block;
  text-align: center;
  height: 68px;
  line-height: 68px;
  font-size: 18px;
  text-transform: uppercase;
  color: $clr-white;
  font-weight: bold;
  padding: 0 55px;
  background: $clr-red;
  transition: all ease 200ms;
  cursor: pointer;
  letter-spacing: 1px;
  position: relative;
  &:hover {
    background: $clr-white;
    color: $clr-red;
  }

  &-buy {
    &:before {
      content: 'купить';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      line-height: 68px;
      font-size: 18px;
      text-transform: uppercase;
      color: transparent;
      font-weight: bold;
      transition: all ease 200ms;
    }
    &:hover {
      color: transparent;
      &:before {
        color: $clr-red;
        transition: all ease 200ms;
      }
    }
  }

}

.btn-prev,
.btn-next {
  width: 88px;
  padding: 0;
  float: left;
  margin-right: 1px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;

  &:hover {
    .ico-next:after,
    .ico-prev:after,
    .ico-next:before,
    .ico-prev:before,
    .ico-next>div:after,
    .ico-prev>div:after,
    .ico-next>div:before,
    .ico-prev>div:before {
      background: $clr-red;
    }
  }
}