.slider {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;

  .slide {
    min-width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: flex-end;
    pointer-events: none;
    img {
      height: 100%;
      width: calc((100% - #{$siteWidth})/2 + #{$siteWidth}*0.6 - 3px);  // TODO: Hardcoded 3px - not ok
    }
  }
}