$font: Helvetica,'Open Sans',Arial,sans-serif;
$fontOS: 'Open Sans', sans-serif;

body {
  font-family: $font;
  color: $clr-black;
  font-size: 16px;
}

a {
  font-family: $font;
  color: $clr-black;
  font-size: 16px;
  text-decoration: none;
}
p {
  color: $clr-grey;
  font-size: 14px;
  text-shadow: 0 0 0 rgba(84,84,84,.5);
  letter-spacing: .3px;
  line-height: 19px;
}