.home {
  width: 100%;
  height: 50vmax;

  .site-width {
    position: relative;
  }

  &__cover {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: $clr-red;
    text-align: center;

    svg {
      margin-top: 150px;
      max-width: 40%;
      max-height: calc(100% - 150px);
      fill: $clr-black;
      opacity: .34;
    }

    img {
      opacity: .34;
      width: 100%;
      height: 100%;
    }
  }

  &__heading {
    color: #FFF;
    display: block;
    position: absolute;
    width: 100%;
    left: -50px;
    bottom: 200px;
    height: 240px;
    font-weight: bold;
    padding-left: 100px;
    z-index: 10;

    &-cover {
      width: 370px;
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      background: $clr-red;

      &:after {
        content: '';
        position: absolute;
        width: 10px;
        height: 10px;
        background: $clr-red;
        left: 100%;
        top: 100%;
      }
    }

    h1 {
      position: absolute;
      display: block;
      top: 25px;
      font-size: 226px;
      line-height: 75px;

      i {
        display: inline-block;
      }

      span {
        display: inline-block;
        white-space: nowrap;
        color: $clr-red;
        margin-left: 305px;
      }
    }
  }

  &__text {
    position: absolute;
    height: 310px;
    width: 100%;
    bottom: 0;
    background: $clr-white;
    padding: 150px 95px 64px 340px;
    display: flex;
    justify-content: space-between;

    p {
      display: inline-block;
      vertical-align: top;
      max-width: 45%;
      width: 100%;
    }
  }

}