.coffee {

  .site-width {
    position: relative;

    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      right: calc(100% - 15px);
      height: 100%;
      width: calc(50vw - 50% + 15px);
      background: $bg-dark;
    }
    &:after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: calc(100% - 15px);
      height: 100%;
      width: calc(50vw - 50% + 15px);
      background: $bg-grey;
    }
  }

  &__list {
    float: left;
    width: 40%;
    height: 100%;
    background: $bg-dark;
    padding-top: 118px;
    padding-right: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    h2 {
      font-weight: bold;
      font-size: 36px;
      color: $clr-white;
      display: inline-block;
      width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      margin-bottom: 65px;
    }

    &-item {
      position: relative;
      float: left;
      width: 152px;
      max-width: 33.3%;
      height: 187px;
      cursor: pointer;

    }

  }

  &__item {
    float: right;
    width: 60%;
    height: 100%;
    background: $bg-grey;
    padding: 210px 0 0 40px;
    display: flex;

    &-description {
      padding: 15px 0 0 30px;
      width: calc(100% - 345px);
      .coffee {
        &__number {
          color: $clr-red;
          font-size: 32px;
          font-weight: bold;
          margin-bottom: 15px;
          display: inline-block;
          width: 100%;
          i {
            font-size: 18px;
            font-weight: normal;
          }
        }
        &__name {
          color: $clr-white;
          font-size: 36px;
          font-weight: bold;
          display: inline-block;
          width: 100%;
          margin-bottom: 50px;
        }
        &__about {
          margin-bottom: 35px;
          p {
            color: #FFF;
            margin-bottom: 15px;
            line-height: 1.6;
            //text-shadow: 0 0 0 rgba(255,255,255,.5);
          }
          .separator {
            width: 10px;
            height: 10px;
            display: block;
            background: $clr-red;
            margin-bottom: 15px;
          }
        }
        &__price {
          font-size: 24px;
          font-weight: bold;
          color: $clr-white;
          margin-bottom: 30px;
        }
      }
    }
  }

}

.landing-area {
  float: left;
  width: 345px;
  height: 590px;

}

.list-item {
  position: absolute;
  text-align: center;
  left: 12px;
  top: 5px;
  transform-origin: left top;
  transform: translate3d(0,0,0) scale(.263);
  will-change: transform;
  transition: transform ease 500ms;

  .active & {
    z-index: 30;
  }

  > * {
    position: absolute;
  }
  svg {
    width: 58px;
  }

  &__id {
    font-size: 12px;
    color: $clr-white;
    font-weight: bold;
    display: block;
    width: calc(100% - 14px);
    height: calc(100% - 25px);
    position: relative;

    span {
      display: block;
      position: relative;
      top: 16px;
      left: -2px;
    }
    &:before {
      content: '';
      display: block;
      position: absolute;
      width: 70px;
      height: 44px;
      left: -12px;
      top: 0;
      background: $clr-red;
      z-index: 0;
      transform: scale(.4, .2) translateX(55px) translateY(86px);
      transform-origin: left top;
      transition: transform ease 200ms;
    }

    .coffee__list-item:hover & {
        &:before {
          transition: transform ease 200ms;
          transform: scale(1, 1) translateX(0) translateY(0);
        }
    }
    .coffee__list-item.active & {
      &:before {
        transition: transform ease 200ms;
        transform: scale(2.03, 3.64) translateX(0) translateY(0);
      }
    }

  }

  &__img {
    position: relative;
  }
  &__logo {
    left: 50%;
    margin-left: -16px;
    top: 213px;
  }
  &__sweeter {
    left: 50%;
    margin-left: -16px;
    top: 309px;
  }
  &__togo {
    width: 100%;
    text-align: center;
    padding-left: 29px;
    top: 323px;
    text-transform: uppercase;
    font-size: 5px;
    i {
      font-size: 9px;
      position: relative;
      top: 1px;
      margin: 0 2px;
    }
  }
  &__name {
    text-transform: uppercase;
    font-size: 27px;
    text-align: center;
    width: 100%;
    font-weight: 800;
    padding-left: calc(10% + 29px);
    padding-right: 10%;
    bottom: 188px;
  }
  &__site {
    width: 100%;
    text-align: center;
    padding-left: 29px;
    bottom: 165px;
    font-size: 8px;
    font-weight: bold;
  }
  &__weight {
    width: 100%;
    text-align: center;
    padding-left: 29px;
    bottom: 147px;
    font-size: 12px;
    font-weight: 800;
  }

}






















