.header {
  height: 90px;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;

  & > .site-width {
    padding: 19px 115px;
    width: 1400px;
    position: relative;
    background: $clr-white;
    transition: all ease 200ms;

    @include breakpoint(1800px) {
      width: 1300px;
      padding: 19px 65px;
    }
    @include breakpoint(1700px) {
      width: 1200px;
      padding: 19px 15px;
    }

    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      right: 100%;
      height: 100%;
      width: calc(50vw - 50%);
      background: $clr-white;
    }
  }

  &__logo {
    float: left;
    display: flex;
    height: 100%;
    align-items: center;

    svg {
      fill: $clr-red;
    }

    &-logo {
      margin-right: 21px;
      svg {
        height: 52px;
      }
    }
    &-sweeter {
      margin-right: 40px;
      svg {
        height: 16px;
      }
    }
  }

  nav {
    float: right;
    height: 100%;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      float: left;
      padding: 0 25px;
      text-shadow: 0 0 0 rgba(0,0,0,.5);

      &:last-child {
        padding-right: 0;
      }
    }
  }

  &__social {
    position: absolute;
    height: 100%;
    top: 0;
    left: 100%;
    margin-left: 20px;
    display: flex;
    justify-content: space-between;
    width: 150px;

    a {
      display: flex;
      height: 100%;
      justify-content: center;
      align-items: center;
      padding: 0 13px;
      svg {
        fill: $clr-white;
      }
    }
  }

}