.ico-ok {
  width: 40px;
  height: 30px;
  position: relative;
  &>div {
    width: 100%;
    height: 100%;
  }
  &:before,
  &:after,
  &>div:before,
  &>div:after {
    width: 10px;
    height: 10px;
    position: absolute;
    background: $clr-red;
    content: '';
    display: block;
  }
  &:before {
    left: 0;
    top: 10px;
  }
  &:after {
    left: 10px;
    top: 20px;
  }
  &>div:before {
    left: 20px;
    top: 10px;
  }
  &>div:after {
    left: 30px;
    top: 0;
  }
}

.ico-prev {
  width: 24px;
  height: 20px;
  position: relative;
  &>div {
    width: 100%;
    height: 100%;
  }
  &:before,
  &:after,
  &>div:before,
  &>div:after {
    width: 4px;
    height: 4px;
    position: absolute;
    background: $clr-white;
    content: '';
    display: block;
  }
  &:before {
    width: 100%;
    left: 0;
    top: 8px;
  }
  &:after {
    height: 12px;
    left: 4px;
    top: 4px;
  }
  &>div:before {
    left: 8px;
    top: 0;
  }
  &>div:after {
    left: 8px;
    top: 16px;
  }
}
.ico-next {
  width: 24px;
  height: 20px;
  position: relative;
  &>div {
    width: 100%;
    height: 100%;
  }
  &:before,
  &:after,
  &>div:before,
  &>div:after {
    width: 4px;
    height: 4px;
    position: absolute;
    background: $clr-white;
    content: '';
    display: block;
  }
  &:before {
    width: 100%;
    left: 0;
    top: 8px;
  }
  &:after {
    height: 12px;
    left: 16px;
    top: 4px;
  }
  &>div:before {
    left: 12px;
    top: 0;
  }
  &>div:after {
    left: 12px;
    top: 16px;
  }
}