.shares {

  &-article {
    width: 40%;
    float: left;
    padding: 135px 30px 100px;

    h2 {
      font-size: 30px;
      font-family: $fontOS;
      color: $bg-dark;
      font-weight: bold;
      margin-bottom: 18px;
    }
  }
  &-top,
  &-finish {
    font-family: $fontOS;
    font-size: 14px;
    font-weight: bold;
    color: $clr-red;
    margin-bottom: 12px;
    display: inline-block;
  }
  &-top {
    color: $clr-red;
  }
  &-finish {
    color: $bg-dark;
    margin-bottom: 55px;
  }
  &-separator {
    margin-bottom: 50px;
  }

  &-message {
    font-family: $fontOS;
    font-size: 16px;
    font-weight: 300;
    line-height: 1.2;
    margin-bottom: 85px;
    p {
      margin-bottom: 15px;
    }
  }

  .push-it {
    height: 68px;
    padding-left: 50px;
    font-size: 14px;
    color: $clr-red;
    font-weight: bold;
    font-family: $fontOS;
    display: flex;
    align-items: center;
  }

}